import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, ToastController, AlertController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { RegisterPage } from '../register/register';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  error: any;
  rerror : any;
  form: any;
  formr : any;
  reg: any;
  mode : any = 0;
  newmode : any = '0';

  constructor(public loadingCtrl: LoadingController, public alertCtrl : AlertController, public navCtrl: NavController, public toastCtrl : ToastController, public connect: Connect, public navParams: NavParams) {
    this.newmode = '0';
    this.mode=0;
    this.form = {
      email: '',
      password: ''
    }
    this.formr = {
      id : '',
      firstn: '',
      lastn: '',
      email:'',
      password:''
    }
    this.error='';
    this.rerror='';
    
  }

  private presentToast(text) {
    let toast = this.toastCtrl.create({
      message: text,
      duration: 2000,
      position: 'top'
    });
    toast.present();
  }

  ionViewDidEnter() {
    if (window.localStorage.getItem('userTag'))
      this.navCtrl.pop();
  }

  login() {
    var msg='';
    if (this.form.password.length<4)
      msg='Password Too Short';
  
    var a1 = this.form.email.indexOf('@');
    if (a1>0) {
      var m = this.form.email.substr(a1);
      if (m.indexOf('.')<=0) 
        msg='Invalid Email Address';
    }
    else msg='Invalid Email Address';
    if (msg.length) 
      this.presentToast(msg);
    else
     this.dologin();
  }

  register() {
    var msg='';
    if (this.formr.password.length<4)
      msg='Password Too Short';
  
    var a1 = this.formr.email.indexOf('@');
    if (a1>0) {
      var m = this.formr.email.substr(a1);
      if (m.indexOf('.')<=0) 
        msg='Invalid Email Address';
    }
    else msg='Invalid Email Address';
    if (msg.length) 
      this.presentToast(msg);
    else
     this.doreg();
  }


  doreg() {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();
    let that = this;
    let url='userreg.php?params='+encodeURI(this.formr.email+'^'+this.formr.password+'^'+this.formr.firstn+'^'+this.formr.lastn);
console.log(url);
    this.connect.getYum(url).subscribe(
          data => {
            loading.dismiss();
 console.log(data);
            if (data.success > '0') {
  	        window.localStorage.setItem('userTag',data.userTag);
                window.localStorage.setItem('myname',data.firstname);
               that.navCtrl.pop();
            }
            else {
                that.rerror = data.error; 
            }
          },
          err => this.connect.logError(err)
        );
  }


  dologin() {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();
    let that = this;
    this.connect.getYum('userlogin.php?params='+encodeURI(this.form.email+'^'+this.form.password)).subscribe(
          data => {
            loading.dismiss();
// console.log(data);
            if (data.success > '0') {
              if ((data.firstname.length > 0) && (data.surname.length>0)) {  
  	        window.localStorage.setItem('userTag',data.userTag);
                window.localStorage.setItem('myname',data.firstname);
               that.navCtrl.pop();
              }
              else {
               that.reg.id=data.userTag;
               that.newmode = '1';
               that.reg.email = data.email;
               that.reg.firstname=data.firstname;
               that.reg.surname = data.surname;
               that.reg.phone = data.phone;
               that.navCtrl.push(RegisterPage,{reg:that.reg});
               window.localStorage.setItem('myname','');
              }
            }
            else {
                that.error = data.error; 
            }
          },
          err => this.connect.logError(err)
        );
  }




  forgot() {

    let em = this.form.email;
    let that = this;
    if (!em.length) 
      that.presentToast('Please Enter Email Address First');
    else {
     let loading = this.loadingCtrl.create({
      content: 'Please wait...'
     });
     loading.present();
     this.connect.getList('zs_forgotpass.php?email='+encodeURI(this.form.email)).subscribe(
          data => {
            loading.dismiss();
            that.doComplete();
          },
          err => this.connect.logError(err)
        );
    }
  }

  doComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Forgot Password',
      subTitle: 'Please check your email for instructions',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }


}






