import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import { Connect } from '../../providers/connect';

/**
 * Generated class for the OneitemPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-oneitem',
  templateUrl: 'oneitem.html',
})
export class OneitemPage {

  myPic : any = './assets/img/blank.jpg';
  sel1 : any = './assets/imgs/sel1.png';
  sel0 : any = './assets/imgs/sel0.png';
  chk1 : any = './assets/imgs/chk1.png';
  chk0 : any = './assets/imgs/chk0.png';

  item: any = 0;
  title: any = '';
  price: any = '';
  totalprice : any = 0.00;
  choices: any;
  description: any = '';
  buttonmode: any = 'Select';
  qty : any = 1;
  notes : any = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, public view: ViewController, private connect: Connect, private zone: NgZone, public cdr: ChangeDetectorRef) {
     this.item = this.navParams.get('item');
     this.buttonmode = this.navParams.get('buttonmode');
     this.description = 'Loading ...';
     this.choices=[];
     this.totalprice=0.00;
     this.qty=1;
     this.notes = '';
  }

  ionViewDidLoad() {
        let that=this;
        this.connect.getList('itemdetails.php?id='+this.item).subscribe(
         data => {
           that.zone.run(() => {
             that.description = data.description;
             that.price = data.price;
             that.title = data.name;
             that.myPic = data.pic;
             that.choices=data.choices;
             that.qty=1;
             that.recalc();
             that.cdr.markForCheck();
           });
         },
         err => this.connect.logError(err)
        );

  }

 
  setsel(c,a)
  { let j = this.choices[c];
    let sz = j.answers.length;
    for (let e=0; e<sz; e++) 
       this.choices[c].answers[e].chosen = (e==a ? true : false);
    this.recalc();
  }


  recalc()
  { let p = parseFloat(this.price);
    let szc=this.choices.length;
    for (let e=0; e<szc; e++) {
      let th=this.choices[e];
      let sza=th.answers.length;
      for (let i=0; i<sza; i++) 
       if (th.answers[i].chosen) 
        if (th.answers[i].extr.length) { 
          let ll=th.answers[i].extr;
          p = p + parseFloat(ll.substr(3,ll.length-4));    
        }
     }
     p*=this.qty;
     this.totalprice = p.toFixed(2);
  }


  addqty(m)
  { this.qty+=m;
    if (this.qty<0) this.qty=0;
    this.recalc();
  }

  closeme()
  { this.view.dismiss();
  }

 addProduct()
 { 
   
    var c = window.localStorage.getItem('cart');
    if (!c) c='';
    let chs = [];
    let szc = this.choices.length;
    for (let e = 0; e<szc; e++) {
      let ths = this.choices[e];
      let sza = ths.answers.length;
      for (let i =0; i<sza; i++) 
      if (ths.answers[i].chosen) 
        chs.push(ths.answers[i].id);
    }

    var nw = this.item.toString()+'^'+this.qty+'^'+ this.totalprice + '^' + this.notes +  '^' + JSON.stringify(chs);
   console.log(nw);
    if (c.length)
      c=c+'~'+nw;
    else
      c=nw;
    window.localStorage.setItem('cart',c);
   this.closeme();
 }

}



