import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { NavController, NavParams, AlertController, MenuController, LoadingController, ModalController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { OneitemPage } from '../oneitem/oneitem';
import { LoginPage } from '../login/login';
import { CheckoutPage } from '../checkout/checkout';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

 public me : any = 0;
 public specials: any[];
 public spectext: any = '';
 public total : any = 0;
 public mcats : any[]; 
 public mitems : any[];
 public items : any[];
 public myname: any='';
 public freightAmount : any=0;
 public freightValid: boolean = false;
 public userPostcode : any = '';
 public mytext : any = '';
 public mode : any = 0;
 public menumode : any = 0;
 public catname: any = '';
 public message: any = 'Welcome to Thai Orchid.  Our app allows you to order easily and receive special offers from us.';
  constructor(public nav: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, public modalCtrl: ModalController, public menuCtrl: MenuController, private connect: Connect, private zone: NgZone, public alertCtrl: AlertController, public cdr: ChangeDetectorRef) {
    this.mcats=[];
    this.items=[];
    this.mytext='';
    this.mode=0;
    this.menumode=0; 
    this.specials=[];
    this.spectext='';
    this.message='Welcome to Thai Orchid.  Our app allows you to order easily and receive special offers from us.';
  }
  
  ionViewDidEnter()  {
         let that=this;
         
      let w = window.localStorage.getItem('userTag');
      if (!w) w='0';
      this.me=parseInt(w);
   let m = window.localStorage.getItem('myname');
        if (!m) m='';
        this.myname=m;
    let s = window.localStorage.getItem('spectext');
    if (!s) s='';
    this.spectext=s;

        let url = 'getcats.php';
        this.connect.getList(url).subscribe(
         data => {
           that.mytext='';
           that.zone.run(() => {
             that.mcats = data.mcats;
             that.message = data.message;    
             that.gocart(0,0);         
           });
         },
         err => {
            that.mytext='';
            this.connect.logError(err)
         }
        ); 

  }
 
  login()
  { this.nav.push(LoginPage);
  }



  delitem(x)
  {  let f = window.localStorage.getItem('cart').split('~');
     let sz=f.length;
     let nw='';
     for (let e=0; e<sz; e++) 
       nw=nw+(e==x ? '' : (nw.length ? '~' : '')+f[e]);
     window.localStorage.setItem('cart',nw);
     this.gocart(0,1);
  }

  delspec(x)
  { let y=x+10000;
    this.gocart(y,1);
  }

  gocart(a,b)  {
         let that=this;
         
      let w = window.localStorage.getItem('userTag');
      if (!w) w='0';
        let url = 'gocart.php?me='+w+'&cart='+window.localStorage.getItem('cart')+'&spectext='+this.spectext+'&newspec='+a;
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
              that.total=data.total;
              that.items=data.items;
              that.specials=data.specials;
              that.spectext=data.spectext;
		 window.localStorage.setItem('spectext',that.spectext);
    
              if (b) that.menumode=1;
           });
         },
         err => {
            that.mytext='';
            this.connect.logError(err)
         }
        ); 

  }


 checkout()
 {  window.localStorage.setItem('spectext',this.spectext);
    if (!this.me) this.login();
    else this.nav.push(CheckoutPage,{total: this.total});
 } 

  gospecials()  {
        let that=this;
               let w = window.localStorage.getItem('userTag');
      if (!w) w='0';
       this.me = parseInt(w);

        let m = window.localStorage.getItem('myname');
        if (!m) m='';
        this.myname=m;


        let url = 'gospec.php?me='+w+'&cart='+window.localStorage.getItem('cart')+'&spectext='+this.spectext;


        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
              that.specials=data.specials;
              that.menumode=2;
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 

  }


    signOutAlert() {
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'Are you sure?',
      subTitle: '',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          role: 'cancel',
          handler: () => {
            that.doLogout();
          }
        }
      ]
    });
    alert.present();
  }

  doLogout()
  {
     window.localStorage.removeItem('userTag');
     window.localStorage.removeItem('cart');

  }

  showItem(data)
  {
    this.nav.push(OneitemPage,{item: data, buttonmode: 'ADD TO ORDER'});
  }

  showCat(data)
  { 
       this.catname = data.name.toUpperCase();
       let that=this;
          
       let url = 'getitems.php?id='+data.id;
        this.connect.getList(url).subscribe(
         data => {
           that.mytext='';
           that.zone.run(() => {
             that.mitems = data.mitems;
             that.mode=1;
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 
  }

}



