import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, AlertController  } from 'ionic-angular';
import { EnterCreditCardPage } from '../enter-credit-card/enter-credit-card';
import { Connect } from '../../providers/connect';

@IonicPage()
@Component({
  selector: 'page-checkout',
  templateUrl: 'checkout.html',
})
export class CheckoutPage {
  public user : any = '';
  public address : any = '';
  public username : any = '';
  public shipping : any = '';
  public payment : any = '';
  public paycard : any = '';
  public pmode : any = 1;
  public notes : any = '';
 public stripe_pub: any = '';
 public total : any = 0.00;
 public allok : boolean = false;

  public methods: any[];
  public pmethods: any[];

  constructor(public navCtrl: NavController, public cdr: ChangeDetectorRef, public alertCtrl: AlertController, public navParams: NavParams, public loadingCtrl: LoadingController, public connect: Connect, private zone: NgZone) {
   this.methods = [];
   this.total = this.navParams.get('total');
   if (this.total< 0.01) this.allok = true;
  }

  ionViewDidEnter() {
    this.recalc();
  }

 showPayment(a)
  { window.localStorage.setItem('userPayment',a);
    this.pmode=0;
    this.recalc();
  }
  
  showEnterPayment() {
    this.navCtrl.push(EnterCreditCardPage, {stripe_pub : this.stripe_pub});
  }


  recalc() {
     let that = this;
     this.payment = window.localStorage.getItem('userPayment');
     if (!this.payment)
       this.payment='';
       this.connect.getYum('getuserdetails.php?me='+window.localStorage.getItem('userTag')+'&pmode='+this.pmode+'&payment='+this.payment).subscribe(
         data => {
           that.zone.run(() => {
             that.methods = data.methods;
             that.paycard = data.paycard;
             that.pmethods = data.pmethods;
             that.stripe_pub = data.stripe_pub;
             that.payment = data.payment;
             if ((that.payment.length) || (that.total < 0.01)) that.allok=true;
             else that.allok=false; 
             window.localStorage.setItem('userPayment',that.payment);
                 that.cdr.markForCheck();
           });
         },
         err => { 
                this.connect.logError(err);
         }
        );  
  }

  myAlert(msg) {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: msg,
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }

  doConfirm() {
            let that = this;
	    let loader = this.loadingCtrl.create({
	      content: "Please wait..."
	    });
	    loader.present();
            let url='saveorder.php?user='+window.localStorage.getItem('userTag')+'^'+this.address+'^'+this.payment+'&cart='+window.localStorage.getItem('cart')+'&spec='+window.localStorage.getItem('spectext')+'&notes='+encodeURI(this.notes);
            console.log(url);
             this.connect.getList(url).subscribe(
	         data => {
		    loader.dismiss();
                    if (data.success) 
                      this.orderComplete();
                    else
                      this.myAlert('Your order could not be completed.  Please try again later');
                 },
	         err => {
                    loader.dismiss();
                    this.connect.logError(err);
                 }
	        );


  }

  orderComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: 'Your order has been received',
      buttons: [
        {
          text: 'Okay',
	  role: 'cancel',
          handler: () => {
             window.localStorage.removeItem('cart');
             window.localStorage.removeItem('spectext');
             that.navCtrl.pop(); 
          }
        }
      ]
    });
    alert.present();
  }

}



