import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {

  reg: any;
  constructor(public loadingCtrl: LoadingController, public navCtrl: NavController, public toastCtrl : ToastController, public connect: Connect, public navParams: NavParams) {

    this.reg = this.navParams.get('reg');
    
  }

  private presentToast(text) {
    let toast = this.toastCtrl.create({
      message: text,
      duration: 2000,
      position: 'top'
    });
    toast.present();
  }

  ionViewDidLoad() {
//    console.log('ionViewDidLoad EmailLoginPage');
  }

  doRegister()
  { let msg='';
    if ((this.reg.firstname.length<1) || (this.reg.surname.length<1)) 
      msg='Please enter your name';
    if (msg.length) 
      this.presentToast(msg);
    else {
       let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'userregister.php?reg='+JSON.stringify(this.reg);
// console.log(url);
       window.localStorage.setItem('myname',this.reg.firstname);
       this.connect.getList(url).subscribe(
          data => {
            loading.dismiss();
            if (data.success) {
// console.log(data);
	       window.localStorage.setItem('userTag',that.reg.id);
              that.navCtrl.pop();
            }
          },
          err => {
            loading.dismiss();
            that.connect.logError(err)
          }
        );

    }
  }

  goBack()
  { this.navCtrl.pop();
  }

}





