import { Component, NgZone, ChangeDetectorRef  } from '@angular/core';
import { IonicPage, NavController, ViewController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { Stripe } from '@ionic-native/stripe'; //https://ionicframework.com/docs/native/stripe/

@IonicPage()
@Component({
  selector: 'page-enter-credit-card',
  templateUrl: 'enter-credit-card.html'
})
export class EnterCreditCardPage {

  masks: any;
  public cardNumber: any = "";
  public cardExpiryMon: any = "";
  public cardExpiryYr: any = "";
  public cardCvc: any="";
  public errMessage: any="";
  public makeDefault: boolean = false;
  public donealready=false;
  public stripe_pub : any = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, public view: ViewController, public connect: Connect, private zone: NgZone, private stripe: Stripe, public cdr: ChangeDetectorRef, public loadingCtrl: LoadingController, public alertCtrl: AlertController) {

    this.stripe_pub = this.navParams.get('stripe_pub');
 
    this.stripe.setPublishableKey(this.stripe_pub);
  }

  getToken() {

    if (!this.donealready) {
      this.donealready=true;

    let loader = this.loadingCtrl.create({
      content: "Please wait..."
    });
    loader.present();

    if (this.cardExpiryMon.length<2) this.cardExpiryMon='0' + this.cardExpiryMon;


    let card = {
      number: this.cardNumber,
      expMonth: this.cardExpiryMon.substr(0,2),
      expYear: 20 + this.cardExpiryYr.substr(0,2),
      cvc: this.cardCvc
    };

    var def;
    if (this.makeDefault){
      def = 1;
    } else {
      def = 0;
    }
   let that = this;
    this.stripe.createCardToken(card)
    .then(token => {

        this.connect.getYum('newcc.php?tag='+window.localStorage.getItem('userTag')+'&token='+JSON.stringify(token)+'&dflt='+def).subscribe(
         data => {
	     that.zone.run(() => {
		 setTimeout(() => {
		        loader.dismiss();
		        that.view.dismiss();
	         }, 200);
                 that.cdr.markForCheck();
             });

         },
         err => {
          this.connect.logError(err);
	     that.zone.run(() => {
		 setTimeout(() => {
		        loader.dismiss();
                        that.view.dismiss();
	         }, 200);
                 that.cdr.markForCheck();
             });
         }
        );
      }
     )
    .catch(error => {
       loader.dismiss();
       that.errMessage = error;
       that.donealready = false;
     });
    }
  }

  ionViewDidLoad() {

  }

  closeme()
  {
    this.view.dismiss();
  }

   showAlert(msg) {
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'error',
      subTitle: msg,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }
}



